import { lOGGED_USER } from "../../actionsType/auth.actionTypes/auth.actions"

const initialState = {
    isAuth: false,
    name: '',
    surname: '',
    id: ''
}

export default (state = initialState, action) => {
    switch(action.type) {
        case lOGGED_USER: {
            console.log(action.payload.check)
            return{...state,isAuth:action.payload.check}
        }
        default: return state
    }
}