import React from 'react'
import { Form, Select } from 'antd';

const { Option } = Select;



const DropdrownInput = ({list=[1,2,3], name, width,onchange = ()=>{},req=false}) => {
  const [form] = Form.useForm();

  const onGenderChange = value => {
   onchange(value)
  }; 

  
  return (
      <Form.Item name={name || null} label="" rules={[{ required: req || false }]}>
        <Select
          onChange={onGenderChange}
          allowClear
          defaultValue={list[0] || null}
          style={{width: width || null}}
        >
          {
            list.map((elem) => {
             return <Option value={elem}>{elem}</Option>
            })
          }
        </Select>
      </Form.Item>
  );
};

export default DropdrownInput;