import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import * as serviceWorker from './serviceWorker';
import { Provider } from 'react-redux';
import store from './store';
import TransactionStatuses from './Widgets/TransactionStatuses/Main';
import ActivityReport from './Widgets/ActivityRePort/Main';
import CrmForm from './Widgets/CrmForm/Main';
import MarketingTable from './Widgets/Marketing_Table/Main';
import { BrowserRouter } from 'react-router-dom';

ReactDOM.render(
  <BrowserRouter>
  <Provider store = {store}>
    <MarketingTable />
  </Provider>
  </BrowserRouter>
    ,
  document.getElementById('root')
);

