import React from 'react';
import { Form,Select,Tag } from 'antd';
import {nanoid} from 'nanoid';

const DropdrownInputSelect = ({list=[], name, req=false}) => {
  const { Option } = Select;
  const children = [];
  for (let i = 0; i < list.length; i++) {
  children.push(<Option key={nanoid()} value={list[i]}>{list[i]}</Option>);
  }

  function handleChange(value) {
    console.log(`selected ${value}`);
  }

  function tagRender(props) {
    const { label, closable, onClose } = props;
  
    return (
      <Tag color='blue' closable={closable} onClose={onClose} style={{ marginRight: 3 }}>
        {label}
      </Tag>
    );
  }
  return (
      <Form.Item name={name || null} label="" rules={[{ required: req || false }]}>
      <Select
      mode="multiple"
      allowClear
      style={{ width: '100%' }}
      onChange={handleChange}
      tagRender={tagRender}
      >
      {children}
     </Select>
    </Form.Item>
  );
};

export default DropdrownInputSelect;