import React,{useEffect, useState} from 'react'
import styles from './style.module.scss';
import {nanoid} from 'nanoid';
import { Select } from 'antd';
import {LeftOutlined,RightOutlined,CloseOutlined } from '@ant-design/icons';
import TableItemsForMarketing_segments from './TableItem';

const Marketing_Segments_table = ({dataSegments, loading}) => {
  const [dataSegment, setDataSegment] = useState([])
  const [allCheck, setAllCheck] = useState(false)
  const { Option } = Select;
  useEffect(()=>{
   const data = dataSegments.map((elem)=> (
     {
       ...elem,
       id: nanoid(),
       isActive: false
     }
   ))
  setDataSegment(data)
  },[dataSegments])
  
  console.log(dataSegments);
  
  function handleChange(value) {
    console.log(value); 
  }
  
  const activeQuantity = dataSegment.filter((elem) => elem.isActive).length

  const handleChagngeActive = (id) => {
    setAllCheck(false)
    const newData = dataSegment
        .map((elem)=> (
          {
            ...elem,
            isActive: id === elem.id ? !elem.isActive : elem.isActive
          }
          ))
          setDataSegment(newData)
        }

  const handleAll = () => {
    setAllCheck(!allCheck)
    
    const newData = !allCheck ? dataSegment
     .map((elem)=> (
      {
        ...elem,
        isActive: true
      }
    )) : 
    dataSegment
     .map((elem)=> (
      {
        ...elem,
        isActive: false
      }
    )) 
    setDataSegment(newData)
  }

    return(
      <div className={styles.table_section_wrapper}>
          <table id={styles.customers}>
            <thead>
            <tr>
              <th><input type="checkbox" onChange={handleAll} checked={allCheck} /> </th>
              <th>НАЗВАНИЕ</th>
              <th>ИСПОЛЬЗОВАЛСЯ</th>
              <th>ПОЛУЧАТЕЛЕЙ</th>
            </tr>
            </thead>
            <tbody>{
              loading
                ? '...loading'
                : dataSegment.map((elem, index) => 
                  <TableItemsForMarketing_segments
                    key={elem.id}
                    date = {elem.createdAt}
                    name={elem.name}
                    quantity={elem.recipients}
                    useQuantity={elem.conditions.length}
                    key={elem.id}
                    id={elem.id}
                    isActive={elem.isActive}
                    handleChagngeActive={handleChagngeActive}
                  />)
              }
            </tbody>
          </table>
          <div className={styles.pagination}>
            <div className={styles.pagination_status}>
            <div className={styles.checked}>ОТМЕЧЕНО: <span>{activeQuantity}/{dataSegments.length}</span></div>
              <div className={styles.checked}>ВСЕГО: <span>{dataSegments.length}</span></div>
              <div className={styles.checked}>СТРАНИЦА: <span>1</span></div>
              <div className={styles.pagination_arrow}>
                  <div><LeftOutlined /> ПРЕДЫДУЩАЯ</div>
                  <a href="#" className={styles.arrow_all}>ВСЕ</a>
                  <div>СЛЕДУЮЩАЯ <RightOutlined /></div>
              </div>
            </div> 
            <div>
              НА СТРАНИЦЕ:
              <Select
                labelInValue
                defaultValue={{ value: '10' }}
                style={{ width: 80 }}
                onChange={handleChange}
              >
                <Option value="5">5</Option>
                <Option value="50">50</Option>
              </Select>
            </div>
          </div>
          <div className={styles.footer}>
          <CloseOutlined className={styles.footer_delIcon} /> УДАЛИТЬ
          </div>
        </div>
    )
};

export default Marketing_Segments_table;
