const initialState = {count: 0,nextPosition: '0px'};
function reducer(state, action) {
  switch (action.type) {
    case 'increment':
      if(state.count === 0) {
        return {count: state.count + 1,nextPosition:'-140px' }
      } else if(state.count === 1) {
        return {count: state.count + 1,nextPosition:'-500px' }
      } else {
        return {count: 2, nextPosition:'-500px'}
      }
    case 'decrement':
      if(state.count === 2) {
        return {count: -1,nextPosition:'-140px' }
      } else if(state.count === 3) {
        return {count: state.count - 1,nextPosition:'-203px' }
      } else {
        return {count: 0, nextPosition:'0px'}
      }
    default:
      throw new Error();
  }
}

