import { Switch, useHistory, useLocation } from 'react-router-dom';
import React from 'react';
import styles from './style.module.scss';

const Marketing_Table_Navbar = () => {
  const location = useLocation();
  const history = useHistory()
  const handleToPage = (e) => {
    history.push(e.currentTarget.name)
  }
    return(
        <div className={styles.navbar}>
         <button
          className={styles.navbar_item}
          style={
            {
              borderBottom: location.pathname === "/" ? "2px solid #2067b0" : "none",
              color: location.pathname === "/" ? "#2067b0" : "#545c6a",
            }
          }
          name="/"
          onClick={handleToPage}
        >
          Рассылки
        </button>
         <button
          className={styles.navbar_item}
          style={
            {
              borderBottom: location.pathname === "/segments" ? "2px solid #2067b0" : "none",
              color: location.pathname === "/segments" ? "#2067b0" : "#545c6a",
            }
          }
          name="/segments"
          onClick={handleToPage}
        >
          Сигменты
          </button>
         <button
          className={styles.navbar_item}
          style={
            {
              borderBottom: location.pathname === "/setting" ? "2px solid #2067b0" : "none",
              color: location.pathname === "/setting" ? "#2067b0" : "#545c6a",
            }
          }
          name="/setting"
          onClick={handleToPage}
        >
          Настройки
        </button>
        </div>
    );
};

export default Marketing_Table_Navbar;
