import React, {useState} from 'react';
import styles from './style.module.scss';
import {DownOutlined} from '@ant-design/icons';
import Marketing_NewsLetter_table from './TableNewsLetter';
import MenuForNewsLetters from './Menu';

const NewsLetters = () => {
 const [toggle, setToggle] = useState(false)
  return (
    <div className={styles.newsLetter}>
      <div className={styles.header}>
        <div className={styles.newsLetter_title}>Рассылки</div>
        <div className={styles.create_section}>
          <button
            type="button"
            className={styles.createBtn}
            onClick={()=> setToggle(!toggle)}
          >
            <span className={styles.createBtn_text}>СОЗДАТЬ РАССЫЛКУ</span>
            <DownOutlined
              className={styles.createBtn_textafter}
              style={{transform: toggle ? 'rotate(180deg)' : 'rotate(0deg)'}}
            />
          </button>
          <div style={{display: toggle ? 'block' : 'none'}}>
            <MenuForNewsLetters/>
          </div>
        </div>
      </div>
      <div>
        <Marketing_NewsLetter_table />
      </div>
    </div>
  )
}

export default NewsLetters
